<template>
  <div class='picket-fixed' v-if="isShow">
    <div class="type-picket phone-pad">
      <div class='flex space_between bar'>
        <img @click.stop='onCancel' class="" src="../../assets/img/face/icon_back.png"/>
        <div @click.stop="onConfirm">确定</div>
      </div>
      <!-- visible-item-count=6 :swipe-duration='1000' item-height=50 -->
      <div class="main">
        <van-picker :columns="columns" @change="onChange" 
        ref='cityPicker'
        item-height=50
        swipe-duration=100
        visible-item-count=6 />
      </div>
    </div>
  </div>
</template>

<script>
  import Picker from 'vant/lib/picker';
  import 'vant/lib/picker/style';
  import {getDealerCityList} from 'fetch/api.js'
  import bridge from 'fetch/bridge.js'
  
  const cities=[];
  
  export default{
    name:'agnetPicker',
    components:{
      [Picker.name]:Picker,
    },
    data(){
      return {
        columns:[],
        city:{},
        current:[],
        isShow:false,
        type:'',
      }
    },
    created(){
      this.type=this.$route.query.type
      if(this.type!='app'){
        this.getDealerCityList()//获取经销商
      }
    },
    methods:{
      changeShow(){
        this.isShow=true;
        setTimeout(()=>{
          if(this.$refs.cityPicker){
            this.current=this.$refs.cityPicker.getValues()
          }
        },100)
      },
      onCancel(){
        this.isShow=false;
      },
      async getDealerCityList(){//获取经销商
        if(this.type=='app'){
          bridge(
          "receiveLoadNetworkArgument",
          {
            path:'dealer/getDealerCityList',gateway:"public",params:{},method:"POST"
          },
          {
            callback:this.getDealerCityListCall,winName:'acceptNewEnergyResultSet'
          }
          );
        }else{
          let res=await getDealerCityList()
          this.getDealerCityListCall(res)
        }
      },
      getDealerCityListCall(res){
        if(res && res.resultCode=='0000'){
          let data=res.data
          //const cities={}
          //const columns=[{ values: Object.keys(cities) }, { values: cities['浙江'] }]
          const columns=[];
          const city={};
          let cityName=''
          this.current=[]
          data.forEach(item=>{
            let province=item.province
            if(!cityName){
              cityName=province
              this.current.push(cityName)
            }
            let c=item.cities;
            city[province]=c;
            cities[province]=[]
            for(let i of c){
              if(this.current.length==1){
                this.current.push(i.city)
              }
              cities[province].push(i.city)
            }
          })
          this.city=city;
          this.columns=[{ values: Object.keys(cities) }, { values: cities[cityName] }]
          console.log('获取到的城市=',{cities,city})
        }else{
          this.$Toast(res.messge)
        }
      },
      onChange(picker, values){
        console.log('val==',{picker, values})
        picker.setColumnValues(1, cities[values[0]]);
        // this.current.push(values[0],this.city[values[0][0].city]);
        // console.log('改变了=',this.city[values[0][0].city])
        this.$nextTick(()=>{
          this.current=this.$refs.cityPicker.getValues()
          console.log('111',this.current)
        })
      },
      onConfirm(){
        console.log('values=',this.current)
        
        let item=this.city[this.current[0]];
        for(let i in item){
          let c=item[i]
          console.log('c===',c,c.city==this.current[1],c.city,this.current[1])
          if(c.city==this.current[1]){
            console.log('选中了==',c)
            this.$emit('getCity',{...c,province:this.current[0]})
            this.onCancel()
            return
          }
        }
      },
    }
  }
</script>

