          invoiceDate:'',
          invoiceFullName:'',
          invoiceIdentificationNumber:'',
          vehicleLicenseUrl:'',
          licenseDate:'',
          licenseFullName:'',
          vehicleType:1,
          orgName:'',
          orgEmail:'',
          orgCode:'',
          authorizationImgUrl:'',
          businessLicenseImgUrl:'',
          unifiedSocialCreditCode:'',//统一社会信用代码
        },
        city:{},
        agentShow:false,//是否展示经销商
        preventRes:false,
        errorNum:0,
        showExample:false,
        type:'',//是否是app
        jumpUrl:'',
        appData:{},//app用户信息
