          }
          // if(this.wayIndex==1){
          //   if(this.codeIndex==0){
          //     iscode=unifiedSocialCreditCode
          //   }
          // }
          let common=vin && (orgName && orgName.length>=2) && iscode && authorizationImgUrl && businessLicenseImgUrl;
          console.log('common=',!!common,!!vin , (orgName && orgName.length>=2) , !!iscode , !!authorizationImgUrl , !!businessLicenseImgUrl)
          if(wayIndex==1){
