      changeCarIndex(car,way=0){
        let i=this.carIndex;
        console.log('this.vehicleList.length==',this.vehicleList.length)
        if(way==0 && this.vehicleList.length==0){
          way=1
        }
        let j=this.wayIndex;
        let json=this.writeArr[i][j]
        console.log('查看==',json,this.writeArr)
        if(!json){
          json={}
        }
        json.agentChoose=this.agentChoose;//经销商
        if(i==0){
          if(j==0){
            json.addCarIndex=this.addCarIndex
          }else if(j==1){
            json.uploadImg1=this.uploadImg1
          }else if(j==2){
            json.uploadImg2=this.uploadImg2
          }
        }
        if(i==1){
          if(j==1){
            json.uploadImg1=this.uploadImg1;
          }else if(j==2){
            json.uploadImg2=this.uploadImg2;
          }
          json.uploadImg4=this.uploadImg4
          json.uploadImg3=this.uploadImg3
        }
        json.writeJson=this.writeJson;
        json.city=this.city
        this.$set(this.writeArr[i],j,json)
        if(car>=0){
          this.carIndex=car;
        }
        this.wayIndex=way;
        let oldJson=this.writeArr[this.carIndex][this.wayIndex]
        console.log('4444',{json,oldJson,way})
        if(oldJson){
          this.writeJson=oldJson.writeJson;
          this.city=oldJson.city;
          this.agentChoose=oldJson.agentChoose;
          let ii=this.carIndex
          let jj=this.wayIndex
          if(jj==1){
            this.uploadImg1=oldJson.uploadImg1
          }else if(jj==2){
            this.uploadImg2=oldJson.uploadImg2
          }
