      async submitBtn(){//提交
        if(!this.btnCol) return
        if(this.preventRes) return;
        this.preventRes=true;
        let carIndex=this.carIndex;//车辆类型 0=>个人车 1=>公司车
        let wayIndex=this.wayIndex;//加车方式 0=>快捷加车 1=>上传购车发票 2=>上传行驶证
        let res=null;
        let img1=this.uploadImg1;
        let img2=this.uploadImg2;
        let params={dealerCode:this.agentChoose.dealerCode};//入会经销商
        this.authAction('mycar_mycar_add_submit')
        if(carIndex==0){//个人
          if(wayIndex==1){//发票
            //params.plateNumber=Base64.encode(img1.plateNumber)
            params.vin=Base64.encode(img1.vin)
            params.invoiceIdentificationNumber=Base64.encode(img1.invoiceIdentificationNumber)
            params.invoiceImgUrl=img1.url;
            params.invoiceDate=img1.invoiceDate+' 00:00:00';
            params.invoiceFullName=Base64.encode(img1.invoiceFullName);
            if(this.$route.query.test==1){
              params.invoiceFullName=Base64.encode("雯青")//测试
            }
            this.writeJson.invoiceFullName=img1.invoiceFullName
            
          }else if(wayIndex==2){//行驶证
            params.vehicleLicenseUrl=img2.url
            params.vin=Base64.encode(img2.vin)
            if(img2.plateNumber){
              params.plateNumber=Base64.encode(img2.plateNumber)
            }
            params.licenseDate=img2.licenseDate+' 00:00:00';
            params.licenseFullName=Base64.encode(img2.licenseFullName)
            if(this.$route.query.test==1){
              params.licenseFullName=Base64.encode("雯青")//测试
              params.vin=Base64.encode('LSGZS83L2MD264765')
            }
            this.writeJson.licenseFullName=img2.licenseFullName
            console.log('行驶证==',params)
          }
        }else if(carIndex==1){//公司
          this.writeJson.vehicleType=this.carIndex+1;
          this.writeJson.invoiceIdentificationNumber=this.writeJson.orgCode;
          let writeJson={...this.writeJson};
          if(wayIndex==1){
            writeJson.invoiceFullName=writeJson.orgName;
          }else{
            writeJson.licenseFullName=writeJson.orgName;
          }
          console.log('writeJson===',writeJson)
          //let reg=/((?=.*[a-zA-Z])(?=.*[0-9]))^[a-zA-Z0-9]{9}$/;
          // if(this.wayIndex==2 || (this.wayIndex==1 && this.codeIndex==1)){
          //   let reg=/^[a-zA-Z0-9]{9}$/;
          //   if(!reg.test(writeJson.orgCode)){
          //     this.preventRes=false
          //     return this.$Toast('组织机构代码填写不正确');
          //   }
          // }
          let vinReg=/((?=.*[a-zA-Z])(?=.*[0-9]))^[a-zA-Z0-9]{17}$/;
          if(writeJson.vin.length!=17){
            //this.vinError=true;
            this.preventRes=false;
            this.authAction('mycar_mycar_add_retry')
            return this.$Toast('VIN码填写不正确');
          }
          if(!vinReg.test(writeJson.vin)) {
            this.preventRes=false
            return this.$Toast('VIN码填写不正确');
          }
          
          for(let i in writeJson){
            if(writeJson[i]){
              let val=writeJson[i]
              if(i=='vin' || i=='plateNumber' || i=='orgEmail' || i=='orgCode' || i=='unifiedSocialCreditCode'
              || i=='licenseFullName' || i=='invoiceFullName' || i=='invoiceIdentificationNumber'){
                if(val){
                  val=Base64.encode(val)
                }
                
              }else if(i=='licenseDate' || i=='invoiceDate'){
                val=val+' 00:00:00';
              }
              if(val){
                params[i]=val
              }
              
            }
          }
          if((this.wayIndex==2 || this.wayIndex==1) && this.codeIndex==1){
            delete params['unifiedSocialCreditCode'];
          }
          if((this.wayIndex==1 || this.wayIndex==2) && this.codeIndex==0){
            delete params['orgCode'];
          }
        }
        this.$LoadingShow('加载中')
        let fn=null;
        let url='userInfo/bindVehicleFast'
        if(carIndex==0 && wayIndex==0){//用户快速加车
          let vin=Base64.encode(this.vehicleList[this.addCarIndex].vin)
          params.vin=vin;
          //return console.log('params快速加车==',params)
          //res=await bindVehicleFast(params)
          fn=bindVehicleFast
        }else if(carIndex==0){//个人车
          //return console.log('params个人车==',params)
          //res=await bindVehicle(params)
          fn=bindVehicle
          url='userInfo/bindVehicle'
        }else if(carIndex==1){//公司车
          console.log('这里是公司的车==',params)
          //res=await bindVehicleApply(params)
          fn=bindVehicleApply
          url='userInfo/bindVehicleApply'
        }
        console.log('上传的参数==params=',params)
        if(this.type=='app'){
          bridge(
          "receiveLoadNetworkArgument",
          {
            path:url,gateway:"private",params:params,method:"POST"
          },
          {
            callback:this.bindCarCall,winName:'acceptNewEnergyResultSet'
          }
          );
        }else{
          res=await fn(params);
          this.bindCarCall(res)
        }
      },
