      bindCarCall(res){
        this.preventRes=false;
        this.$LoadingHide()
        console.log('提交res11=',res)
        
        if(res && res.resultCode=='0000'){
          this.authAction("mycar_mycar_add_submit_done")
          console.log('提交成功==')
          if(this.carIndex==0){
            if(this.type=='app'){
              window.setCarVerifyInfo=(res)=>{
                console.log('加车成功setCarVerifyInfo res=',res)
                if(res){
                  res=JSON.parse(res);
                  if(res.realNameAuthStatus==0){
                    this.jumpUrl=res.jumpUrl;
                    this.addIsSuccess=true;
                    bridge('JoinSuccess')
                  }else{
                    this.toastFn();
                  }
                }else{
                  this.toastFn();
                }
              }
              let vin=''
              let img1=this.uploadImg1;
              let img2=this.uploadImg2;
              let wayIndex=this.wayIndex
              if(wayIndex==1){//发票
                vin=img1.vin
              }else if(wayIndex==2){
                vin=img2.vin
              }else{
                vin=this.vehicleList[this.addCarIndex].vin
              }
              bridge('getCarVerifyInfo',vin)
            }else{
              this.toastFn();
            }
          }else{
            this.isError=true;
            this.errorStatus=5;
          }
        }else{
          this.isError=true;
		  if(res.resultCode=='1090'){
		  	this.errorStatus=13;
			if(this.type!='app'){
				res.message=res.message.replace(/(\n|\r|\\n|\\\\n)/g,'<br/>')
				res.message=res.message.replace(/\d+-\d+-\d+/g,function($1,$2){
					console.log('进行处理==',{$1,$2})
					return `<a style="color:#005FBE;" href="tel:${$1}">${$1}</a>`
				})
			}
		  }else{
			  this.errorStatus=12;
		  }
		  this.errorMes=res.message;
          this.authAction("mycar_mycar_add_submit_fail")
          // this.$Toast(res.message)
        }
      },
