      getUploadMes(res,uploadIndex,tempCarType){//获取到证件信息
        console.log('获取到证件信息0000=',res,uploadIndex,'tempCarType=',tempCarType)
        if(res && res.resultCode=='0000'){
          let {data,key}=res.data;
          let mes={}
          if(uploadIndex==1 || uploadIndex==2){
            mes=JSON.parse(Base64.decode(data))
          }
          console.log('mes===00',mes)
          if(uploadIndex==1){
            let {name,id_number,vehicle_invoice_buyer,vehicle_invoice_issue_date,vehicle_invoice_buyer_id,vehicle_invoice_car_vin,vehicle_invoice_engine_id}=mes
            //this.uploadImg1={url:'',res:false,localImg:'',file:''};
            console.log({name ,vehicle_invoice_buyer ,vehicle_invoice_issue_date , vehicle_invoice_buyer_id , vehicle_invoice_car_vin})
            if(!this.dateTest(vehicle_invoice_issue_date)){//校验日期
              vehicle_invoice_issue_date='';
            }
            let dialogRes=!vehicle_invoice_buyer && !vehicle_invoice_issue_date && !vehicle_invoice_buyer_id && !vehicle_invoice_car_vin;
            let dialogRes1=dialogRes;
            if(this.carIndex==0){
              dialogRes=!(vehicle_invoice_buyer && vehicle_invoice_issue_date && vehicle_invoice_buyer_id && vehicle_invoice_car_vin);
            }
            
            if(dialogRes){
              //this.uploadImg1={url:'',res:false,localImg:'',file:''};
              this.ticketError=true;
              this.loadImgNum=this.loadImgNum+1;
              console.log('捡来=====',this.carIndex==1)
              if(this.carIndex==1){
                this.uploadImg1={url:'',res:false,localImg:'',file:''};
                this.ticketDisabled=true;
                let myjson={
                  vin:'',
                  invoiceImgUrl:'',
                  invoiceDate:'',
                  invoiceFullName:'',
                  invoiceIdentificationNumber:'',
                  orgName:'',
                  vehicleType:this.carIndex+1,
                  orgCode:'',
                  unifiedSocialCreditCode:'',
                }
                this.writeJson={...this.writeJson,...myjson}
                return 
              }
            }
            if(this.carIndex==0 && dialogRes1){
              this.uploadImg1={url:'',res:false,localImg:'',file:''};
              return;
            }
            this.ticketDisabled=false;
            let uploadImg1=this.uploadImg1;
            uploadImg1.res=true;
            uploadImg1.name=name;
            uploadImg1.url=key
            uploadImg1.id_number=id_number;
            
            uploadImg1.invoiceFullName=vehicle_invoice_buyer;
            uploadImg1.invoiceDate=vehicle_invoice_issue_date;
            uploadImg1.invoiceImgUrl=key;
            uploadImg1.invoiceIdentificationNumber=vehicle_invoice_buyer_id;//购车发票证件号码
            uploadImg1.vin=vehicle_invoice_car_vin;
            //uploadImg1.plateNumber=vehicle_invoice_engine_id;
            this.uploadImg1=uploadImg1;
            console.log('uploadImg1===',uploadImg1)
            
            if(tempCarType!=undefined){//公司的车
              let writeJson={
                vin:this.uploadImg1.vin,
                // plateNumber:this.uploadImg1.plateNumber,
                invoiceImgUrl:key,
                invoiceDate:this.uploadImg1.invoiceDate,
                invoiceFullName:this.uploadImg1.invoiceFullName,
                invoiceIdentificationNumber:this.uploadImg1.invoiceIdentificationNumber,
                orgName:mes.vehicle_invoice_buyer,
                vehicleType:this.carIndex+1,
                orgCode:vehicle_invoice_buyer_id,
                unifiedSocialCreditCode:vehicle_invoice_buyer_id,
              }
              this.writeJson={...this.writeJson,...writeJson}
            }
          }else if(uploadIndex==2){
            //uploadImg2:{url:'',res:'',localImg:'',validity:'',startTime:'',endTime:''}
            let {validity,issue_date,vehicle_license_main_register_date,vehicle_license_main_vin,vehicle_license_main_plate_num,vehicle_license_main_owner}=mes
            //this.uploadImg2={url:'',res:false,localImg:'',file:''};
            console.log('行驶证===',{issue_date,vehicle_license_main_vin,vehicle_license_main_plate_num,vehicle_license_main_owner})
            console.log('this.dateTest(issue_date)=',this.dateTest(issue_date));
            if(!this.dateTest(issue_date)){//校验日期
              issue_date='';
            }
            let dialogRes=!issue_date && !vehicle_license_main_vin && !vehicle_license_main_owner;
            let dialogRes1=dialogRes;
            if(this.carIndex==0){
              dialogRes=!(issue_date && vehicle_license_main_vin && vehicle_license_main_owner);
            }
            if(dialogRes){
              //this.uploadImg2={url:'',res:false,localImg:'',file:''};
              this.ticketError=true;
              this.loadImgNum=this.loadImgNum+1;
              if(this.carIndex==1){
                this.uploadImg2={url:'',res:false,localImg:'',file:''};
                this.cardDisabled=true;
                let myjson={
                  vin:'',
                  plateNumber:'',
                  vehicleLicenseUrl:'',
                  licenseDate:'',
                  licenseFullName:'',
                  vehicleType:this.carIndex+1,
                  orgName:'',
                  orgCode:'',
                  unifiedSocialCreditCode:'',
                }
                this.writeJson={...this.writeJson,...myjson};
                return
              }
            }
            if(this.carIndex==0 && dialogRes1){
              this.uploadImg2={url:'',res:false,localImg:'',file:''};
              return;
            }
            this.cardDisabled=false;
            this.uploadImg2.res=true;
            this.uploadImg2.validity=validity;
            this.uploadImg2.licenseDate=issue_date;
            
            this.uploadImg2.url=key
            this.uploadImg2.vin=vehicle_license_main_vin
            this.uploadImg2.plateNumber=vehicle_license_main_plate_num;
            this.uploadImg2.licenseFullName=vehicle_license_main_owner;
            this.uploadImg2.vehicleLicenseUrl=key;//照片
            if(tempCarType!=undefined){//公司的车
              let writeJson={
                vin:this.uploadImg2.vin,
                plateNumber:this.uploadImg2.plateNumber,
                vehicleLicenseUrl:key,
                licenseDate:this.uploadImg2.licenseDate,
                licenseFullName:this.uploadImg2.licenseFullName,
                vehicleType:this.carIndex+1,
                orgName:mes.vehicle_license_main_owner,
                orgCode:'',
                unifiedSocialCreditCode:'',
              }
              this.writeJson={...this.writeJson,...writeJson}
            }
          }else if(uploadIndex==3){
            this.uploadImg3.res=true
            this.uploadImg3.url=key
            this.writeJson={...this.writeJson,businessLicenseImgUrl:key}
          }else if(uploadIndex==4){
            this.uploadImg4.res=true
            this.uploadImg4.url=key
            this.writeJson={...this.writeJson,authorizationImgUrl:key}
          }
        }else{
          let img={url:'',res:false,localImg:'',file:''};
          //let current=null;
          let writeJson=this.writeJson;
          if(uploadIndex==1 || uploadIndex==2){
            this.ticketError=true
            this.authAction('mycar_mycar_add_retry')
            if(uploadIndex==1){
              //current=this.uploadImg1
              this.uploadImg1=img;
            }else{
              //current=this.uploadImg2
              this.uploadImg2=img;
            }
          }else{
            this.isError=true;
            this.errorStatus=12;
            this.errorMes=res.message;
            if(uploadIndex==3){
              //current=this.uploadImg3;
              this.uploadImg3=img;
              writeJson={...writeJson,businessLicenseImgUrl:''};
              console.log('开始清除数据33333')
            }else if(uploadIndex==4){
              console.log('开始清除数据44444')
              //current=this.uploadImg4
              this.uploadImg4=img;
              writeJson={...writeJson,authorizationImgUrl:''};
            }
          }
          //current=img;
          if(this.carIndex==1 && (uploadIndex==1 || uploadIndex==2)){
            if(this.wayIndex==1){
              this.uploadImg1={url:'',res:false,localImg:'',file:''};
              this.ticketDisabled=true;
              if(tempCarType!=undefined){//公司的车
                let newWriteJson={
                  vin:'',
                  invoiceImgUrl:'',
                  invoiceDate:'',
                  invoiceFullName:'',
                  invoiceIdentificationNumber:'',
                  orgName:'',
                  vehicleType:this.carIndex+1,
                }
                writeJson={...writeJson,...newWriteJson}
              }
            }else{
              this.uploadImg2={url:'',res:false,localImg:'',file:''};
              this.cardDisabled=true;
              if(tempCarType!=undefined){//公司的车
                let newWriteJson={
                  vin:'',
                  plateNumber:'',
                  vehicleLicenseUrl:'',
                  licenseDate:'',
                  licenseFullName:'',
                  vehicleType:this.carIndex+1,
                  orgName:'',
                }
                writeJson={...writeJson,...newWriteJson}
              }
            }
          }
          this.writeJson=writeJson;
          this.loadImgNum=this.loadImgNum+1;
        }
        this.$LoadingHide()
      },
      async getFastBindVehicleList(){//查询用户可快捷绑定的车辆列表
        console.log('查询用户可快捷绑定的车辆列表=',this.type,this.type=='app')
        if(this.type=='app'){
          bridge(
          "receiveLoadNetworkArgument",
          {
            path:'userInfo/getFastBindVehicleList',gateway:"private",params:{},method:"GET"
          },
          {
            callback:this.getFastBindVehicleListCall,winName:'acceptNewEnergyResultSet'
          }
          );
        }else{
          let res=await getFastBindVehicleList()
          this.getFastBindVehicleListCall(res)
        }
      },
      getFastBindVehicleListCall(res){
        console.log('查询用户可快捷绑定的车辆列表=res=',res)
        this.$refs.city.getDealerCityList()
        if(res && res.resultCode=='0000'){
          this.vehicleList=res.data;
          if(this.vehicleList.length==0){
            this.wayIndex=1;
          }else{
            this.wayIndex=0;
          }
        }
        // if(this.$route.query.type=='app'){
        //   this.getUserMessage()
        // }
      },
      gotoMunual(){//跳转到人工审核页面
        let wayIndex=this.wayIndex
        let img=this.uploadImg1
        let userName=img.invoiceFullName;
        if(wayIndex==2){
          img=this.uploadImg2
          userName=img.licenseFullName;
        }
        sessionStorage.isMu=0;
        console.log('img===',img,'this.uploadIndex=',this.wayIndex,'sessionStorage.isMu=',sessionStorage.isMu)
        this.$store.commit("SET_IMGFILE",img.file)
        let newJson={...img,userName,file:'',localImg:'',index:this.wayIndex}
        sessionStorage.setItem('uploadImg',JSON.stringify(newJson))
        sessionStorage.setItem('city',JSON.stringify(this.city))
        sessionStorage.setItem('agent',JSON.stringify(this.agentChoose))
        //sessionStorage.uploadImg=JSON.stringify(img)
        this.$router.push({name:'ManualAudit',query:{wayIndex,carIndex:this.carIndex,type:this.type}})
        this.ManualAuditShow=false;
        this.ticketError=false;
        this.authAction('mycar_mycar_add_labor')
      },
      addQuick(i){//选择快捷加车的的车型
        this.addCarIndex=i
      },
      orgNameLen(){//公司名字
        let orgName=this.writeJson.orgName.trim();
        if(orgName.length>100){
          orgName=orgName.substring(0,100);
        }
        this.writeJson.orgName=orgName;
      },
      orgEmailLen(){//公司邮箱
        let orgEmail=this.writeJson.orgEmail.trim();
        if(orgEmail.legnth>50){
          orgEmail=orgEmail.substring(0,50)
        }
        this.writeJson.orgEmail=orgEmail;
      },
      vinLen(){//公司vin
        let vin=this.writeJson.vin.trim();
        if(vin.length>17){
          vin=vin.substring(0,17)
        }
        this.writeJson.vin=vin;
      },
      // orgCodeLen(){//组织机构代码证
      //   let orgCode=this.writeJson.orgCode.trim();
      //   if(orgCode.length>9){
      //     orgCode=orgCode.substring(0,9);
      //   }
      //   this.writeJson.orgCode=orgCode
      // },
      alertMesFn(mes){
        this.toastText=mes
        if(this.alertTime){
          clearTimeout(this.alertTime)
        }
        this.alertTime=setTimeout(()=>{
          this.toastText=''
          this.alertTime=null
        },2000)
      }
    }
  }
</script>

